import { Component, OnInit } from '@angular/core';
import { Employee } from '../models/employee.model';
import { Router  , ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-add-employee',
  templateUrl: './add-employee.component.html',
  styleUrls: ['./add-employee.component.css']
})
export class AddEmployeeComponent implements OnInit {
  employee: Employee = {
    id: null,
    empName: null,
    empAge: null,
    empEmail:null
  }
  private employeeList = [];
  private localdata = [];
  private empId;
  constructor(private route: ActivatedRoute,
    private router: Router) { }

  ngOnInit() {
    this.empId = this.route.params['_value']['id'];
    if(this.empId){
      let existingVal = JSON.parse(localStorage.getItem('Employee'));
      var keyVal = 0;
      existingVal.forEach((item, index)=>{
        if(item.id == this.empId) { keyVal = index};
      });
      this.employee = existingVal[keyVal];
    }
  }

  saveCancel(){
    this.router.navigate(['employees']); 
  }
  saveEmployee(newEmployee: Employee){
    
    if(newEmployee.id){
      let allEmpl = JSON.parse(localStorage.getItem('Employee'));
       let e = allEmpl.map((item)=>{
        if(item.id == this.empId){
          item.empName = newEmployee.empName;
          item.empAge = newEmployee.empAge;
          item.empEmail = newEmployee.empEmail;
        }
        return item;
      }); 
      localStorage.setItem("Employee", JSON.stringify(e));
      this.router.navigate(['employees']);
    }
    else{
      let localVal = localStorage.getItem("Employee");
      if(localVal){
        let existingList = JSON.parse(localVal);
        newEmployee.id = existingList.length+1;
        existingList.push(newEmployee);
        localStorage.setItem("Employee", JSON.stringify(existingList));
      }
      else{
        newEmployee.id = 1;
        let employeeLists = [newEmployee];
        localStorage.setItem("Employee", JSON.stringify(employeeLists));
      }
      this.router.navigate(['employees']);
    }
  }
}



